<template>
  <div class="register">
    <van-nav-bar title="" left-arrow @click-left="onClickLeft"/>
    <van-cell-group>
      <van-cell class="parent-box" value=""/>
      <van-field label="" v-model="username" placeholder="'_'6-30" :error-message="nameErrMsg" clearable/>
      <van-field label="" v-model="password" type="password" placeholder="6-30" :error-message="pwdErrMsg1" clearable/>
      <van-field label="" v-model="checkpassword" type="password" placeholder="" :error-message="pwdErrMsg2" clearable/>
    </van-cell-group>

    <van-cell-group>
      <van-cell class="parent-box parent-cell" value=""/>
      <van-field label="" value="" readonly/>
      <van-field label="" v-model="id_no" placeholder="" :error-message="idErrMsg" clearable/>
    </van-cell-group>

    <van-cell-group>
      <van-cell class="parent-box parent-cell" value=""/>
      <van-field label="" v-model="phone_number" type="tel" placeholder="" :error-message="pnErrMsg" clearable/>
      <van-field label="" v-model="email" placeholder="" :error-message="emailErrMsg" clearable/>
    </van-cell-group>

    <van-cell-group>
      <van-cell class="parent-box parent-cell" value=""/>
      <van-field label="" v-model="role_value" @focus="show = true" readonly>
        <van-popup position="bottom" v-model="show" slot="button">
          <van-picker show-toolbar :columns="columns" @confirm="onConfirm" @cancel="onCancel"/>
        </van-popup>
      </van-field>
    </van-cell-group>

    <van-button class="register-btn" type="primary" @click="register"></van-button>
  </div>
</template>

<script>
import { NavBar, Cell, CellGroup, Field, Button, Popup, Picker, Toast } from 'vant';

export default {
  name: 'register',

  components: {
    [NavBar.name]: NavBar,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Field.name]: Field,
    [Button.name]: Button,
    [Popup.name]: Popup,
    [Picker.name]: Picker,
    [Toast.name]: Toast
  },

  methods: {
    onClickLeft() {
      this.$router.push('login');
    },

    onConfirm(value, index) {
      this.role_value = value;
      this.role = index;
      this.show = false;
    },

    onCancel() {
      this.show = false;
    },

    register() {
      // window.console.log(this.nameErr);
      if (this.isFormLegal()) {
        Toast.loading({
          mask: true,
          duration: 0,
          forbidClick: true,
          loadingType: 'spinner',
          message: '...'
        });

        // api
        this.$axios
          .post('/users/register', {
            username: this.username,
            password: this.password,
            email: this.email,
            phone_number: this.phone_number,
            id_no: this.id_no,
            role: this.role
          })
          // ES6Arrow Function
          .then(res => res.data)
          .then(data => {
            Toast.clear();
            Toast(data.msg);

            if (data.code === 1) this.$router.push('/login');
          });
      } else {
        Toast.fail('');
      }
    },

    // 
    isFormLegal() {
      return (
        this.username &&
        this.password &&
        this.checkpassword &&
        this.email &&
        this.phone_number &&
        this.id_no &&
        this.password == this.checkpassword &&
        this.nameErr == false &&
        this.pwdErr1 == false &&
        this.pwdErr2 == false &&
        this.idErr == false &&
        this.pnErr == false &&
        this.emailErr == false
      );
    },

    // ES6let
    // str+' ' '_'
    checks(str) {
      let pattern = new RegExp("[`~!@#$^&*()=|{}':;',\\[\\].<>/?~@#&*|{}'%+]");
      for (let i = 0; i < str.length; i++) {
        if (!str.substr(i, 1).replace(pattern, '')) {
          return true;
        }
        if (str.substr(i, 1) == ' ') {
          return true;
        }
      }
      return false;
    },

    // str
    checkStrNUm(str) {
      let numReg = /^[0-9]+$/;
      let numRe = new RegExp(numReg);
      for (let i = 0; i < str.length; i++) {
        if (str.substr(i, 1).replace(numRe, '')) {
          return false;
        }
      }
      return true;
    },

    // 
    checksId() {
      if (this.id_no.length == 18) {
        if (
          this.checkStrNUm(this.id_no.substr(0, 17)) &&
          (this.checkStrNUm(this.id_no.substr(17, 1)) || this.id_no[17] === 'x')
        ) {
          return true;
        }
      }
      return false;
    },

    // 
    checksPN() {
      if (this.phone_number.length == 11) {
        if (this.checkStrNUm(this.phone_number)) {
          return true;
        }
      }
      return false;
    },

    // 
    checksEmail() {
      let re = /^(\w-*\.*)+@(\w-?)+(\.\w{2,})+$/;
      if (re.test(this.email)) {
        return true;
      } else {
        return false;
      }
    }
  },

  data() {
    return {
      username: '',
      password: '',
      checkpassword: '',
      pwdErrMsg1: '',
      pwdErrMsg2: '',
      nameErrMsg: '',
      nameErr: true,
      pwdErr1: true,
      pwdErr2: true,
      id_no: '',
      idErrMsg: '',
      idErr: true,
      phone_number: '',
      pnErrMsg: '',
      pnErr: true,
      email: '',
      emailErrMsg: '',
      emailErr: true,
      role: 0,
      role_value: '',
      show: false,
      columns: ['', '', '', '']
    };
  },

  watch: {
    username() {
      if (this.username.length < 6) {
        this.nameErrMsg = '6';
        this.nameErr = true;
      } else if (this.username.length > 30) {
        this.nameErrMsg = '30';
        this.nameErr = true;
      } else if (this.checks(this.username)) {
        this.nameErrMsg = '';
        this.nameErr = true;
      } else {
        this.nameErrMsg = '';
        this.nameErr = false;
      }
    },

    password() {
      if (this.password.length < 6) {
        this.pwdErrMsg1 = '6';
        this.pwdErr1 = true;
      } else if (this.password.length > 30) {
        this.pwdErrMsg1 = '30';
        this.pwdErr1 = true;
      } else {
        this.pwdErrMsg1 = '';
        this.pwdErr1 = false;
      }
    },

    checkpassword() {
      if (this.checkpassword !== this.password) {
        this.pwdErrMsg2 = '';
        this.pwdErr2 = true;
      } else {
        this.pwdErrMsg2 = '';
        this.pwdErr2 = false;
      }
    },

    id_no() {
      if (this.checksId()) {
        this.idErrMsg = '';
        this.idErr = false;
      } else {
        this.idErrMsg = '';
        this.idErr = true;
      }
    },

    phone_number() {
      if (this.checksPN()) {
        this.pnErrMsg = '';
        this.pnErr = false;
      } else {
        this.pnErrMsg = '';
        this.pnErr = true;
      }
    },

    email() {
      if (this.checksEmail()) {
        this.emailErrMsg = '';
        this.emailErr = false;
      } else {
        this.emailErrMsg = '';
        this.emailErr = true;
      }
    }
  }
};
</script>

<style>
.van-nav-bar .van-icon {
  color: white;
}

.register {
  background-color: #fafafa;
}

.register .parent-box {
  border-bottom: 1px solid #ebedf0;
}
.register .parent-box:not(:last-child)::after {
  border: none;
}

.register .van-field {
  font-size: 13px;
  color: #5f6266;
}

.register .van-field__control {
  color: #5f6266;
}

.register-btn {
  margin-bottom: 5%;
}
</style>